import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  Row,
  Col,
  ListGroup,
  Image,
  Form,
  Button,
  Card,
} from "react-bootstrap";
import Message from "../components/Message";
import { addToCart, removeFromCart } from "../actions/cartActions";

const CartScreen = ({ match, location, history }) => {
  const productId = match.params.id;

  const qty = location.search ? Number(location.search.split("=")[2]) : 1;
  const vid = location.search && location.search.split("=")[1].split("?")[0];

  console.log(vid);
  console.log(location.search.split("=", "?"));

  const dispatch = useDispatch();

  const cart = useSelector((state) => state.cart);
  const { cartItems } = cart;

  console.log(cartItems);

  useEffect(() => {
    if (productId) {
      dispatch(addToCart(productId, qty, vid));
    }
  }, [dispatch, productId, qty, vid]);

  const removeFromCartHandler = (id) => {
    dispatch(removeFromCart(id));
  };

  const checkoutHandler = () => {
    history.push("/login?redirect=shipping");
    console.log("checkout");
  };

  return (
    <Row>
      <Col md={8}>
        <h1>Shopping Cart</h1>
        {cartItems.length === 0 ? (
          <Message>
            Your Cart is Empty <Link to='/'>Go Back</Link>
          </Message>
        ) : (
          <ListGroup variant='flush'>
            {cartItems.map((item) => {
              return (
                <ListGroup.Item key={item.product}>
                  <Row>
                    <Col md={2}>
                      <Link to={`/product/${item.product}`}>
                        <Image src={item.image} fluid rounded></Image>
                      </Link>
                    </Col>
                    <Col md={3}>
                      <Row>
                        <h4>{item.option1_value}</h4>
                      </Row>
                      <Row>{item.option2_value}</Row>
                      <Row>{item.name}</Row>
                    </Col>
                    <Col md={2}>Rs.{item.price}</Col>
                    {/* <Col md={2}>
                      <Form>
                        <Button
                          className='bi bi-plus'
                          type='button'
                          onClick={() =>
                            dispatch(
                              addToCart(
                                item.product,
                                Number(item.qty > 1 ? item.qty - 1 : item.qty)
                              )
                            )
                          }
                        >
                          <i className='fas fa-minus-square' />
                        </Button>
                        <h3>{item.qty}</h3>
                        <Button
                          type='button'
                          onClick={() =>
                            dispatch(
                              addToCart(item.product, Number(item.qty + 1))
                            )
                          }
                        >
                          <i className='fas fa-plus-square' />
                        </Button>
                      </Form>
                    </Col> */}
                    <Col md={2}>
                      <Button
                        type='button'
                        variant='light'
                        onClick={() => removeFromCartHandler(item.product)}
                      >
                        <i className='fas fa-trash' />
                      </Button>
                    </Col>
                  </Row>
                </ListGroup.Item>
              );
            })}
          </ListGroup>
        )}
      </Col>
      <Col md={4}>
        <Card>
          <ListGroup variants='flush'>
            <ListGroup.Item>
              <h2>
                Subtotal ({cartItems.reduce((acc, item) => acc + item.qty, 0)})
                items
              </h2>
              Rs.
              {cartItems
                .reduce((acc, item) => acc + item.qty * item.price, 0)
                .toFixed(2)}
            </ListGroup.Item>
            <ListGroup.Item>
              <Button
                type='button'
                className='btn-block'
                disabled={cartItems.length === 0}
                onClick={checkoutHandler}
              >
                Checkout
              </Button>
            </ListGroup.Item>
          </ListGroup>
        </Card>
      </Col>
    </Row>
  );
};

export default CartScreen;
