import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import {
  Row,
  Col,
  Image,
  ListGroup,
  Card,
  Button,
  Form,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  listProductDetails,
  createProductReview,
} from "../actions/productActions";
import Message from "../components/Message";
import Loader from "../components/Loader";
import Rating from "../components/Rating";
import { PRODUCT_CREATE_REVIEW_RESET } from "../constants/productConstants";

const ProductScreen = ({ match, history }) => {
  const [qty, setQty] = useState(1);
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState("");

  const dispatch = useDispatch();

  const productDetails = useSelector((state) => state.productDetails);
  const { error, loading, product } = productDetails;

  const productReviewCreate = useSelector((state) => state.productReviewCreate);
  const { success: successProductReview, error: errorProductReview } =
    productReviewCreate;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  console.log(product)

  useEffect(() => {
    if (successProductReview) {
      
      setRating(0);
      setComment("");
    }
    if (!product._id || product._id !== match.params.id) {
      dispatch(listProductDetails(match.params.id));
      dispatch({ type: PRODUCT_CREATE_REVIEW_RESET });
    }
  }, [dispatch, match, product._id, successProductReview]);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      createProductReview(match.params.id, {
        rating,
        comment,
      })
    );
  };

  // const product = {variants: []}

  const decrement = () => {
    if (qty > 1) {
      setQty((prev) => prev - 1);
    }
  };

  const increment = () => {
    setQty((prev) => prev + 1);
  };

  const addToCartHandler = (vid) => {
    history.push(`/cart/${match.params.id}?vid=${vid}?qty=${qty}`);
  };

  return (
    <>
      <Link className='btn btn-light my-3' to='/'>
        GO BACK
      </Link>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant='danger'>{error}</Message>
      ) : (
        <>
          <Row>
            <Col md={6}>
              <Image src={product.image_url} fluid />
            </Col>
            <Col md={3}>
              <ListGroup.Item>
                <h3>{product.item_name}</h3>
              </ListGroup.Item>
              {product.variants.map((item) => {
                return (
                  <ListGroup key={item.variant_id} variant='flush'>
                    <Row>
                      <ListGroup.Item>{item.option1_value}</ListGroup.Item>
                      <ListGroup.Item>
                        {item.option2_value === true
                          ? item.option2_value
                          : null}
                      </ListGroup.Item>
                      <ListGroup.Item>Rs. {item.default_price}</ListGroup.Item>
                      <Card>
                        <ListGroup variant='flush'>
                          <ListGroup.Item>
                            <Row>
                              <Col></Col>
                              <Col>
                                <strong>{product.item_name}</strong>
                              </Col>
                            </Row>
                          </ListGroup.Item>
                          <ListGroup.Item>
                            <Row>
                              <Col>Qty</Col>

                              {/* <Form.Control
                        as='select'
                        value={qty}
                        onChange={(e) => setQty(e.target.value)}
                      ></Form.Control> */}
                              <Row>
                                <Form>
                                  <Col>
                                    <Button
                                      className='bi bi-plus'
                                      type='button'
                                      onClick={decrement}
                                    >
                                      <i className='fas fa-minus-square' />
                                    </Button>
                                  </Col>
                                  <Col>
                                    <h3>{qty}</h3>
                                  </Col>
                                  <Col>
                                    <Button type='button' onClick={increment}>
                                      <i className='fas fa-plus-square' />
                                    </Button>
                                  </Col>
                                </Form>
                              </Row>
                            </Row>
                          </ListGroup.Item>
                          <ListGroup.Item>
                            <Button
                              onClick={() => addToCartHandler(item.variant_id)}
                              className='btn-block'
                              type='button'
                            >
                              Add To Cart
                            </Button>
                          </ListGroup.Item>
                        </ListGroup>
                      </Card>
                    </Row>
                  </ListGroup>
                );
              })}
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <h2>Reviews</h2>
              {product.reviews.length === 0 && <Message>No Reviews</Message>}
              <ListGroup variant='flush'>
                {product.reviews.map((review) => (
                  <ListGroup.Item key={review._id}>
                    <strong>{review.name}</strong>
                    <Rating value={review.rating}></Rating>
                    <p>{review.createdAt.substring(0, 10)}</p>
                    <p>{review.comment}</p>
                  </ListGroup.Item>
                ))}
                <ListGroup.Item>
                  <h2>Write a Customer Review</h2>
                  {errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}
                  {userInfo ? (
                    <Form onSubmit={submitHandler}>
                      <Form.Group controlId='rating'>
                        <Form.Label>Rating</Form.Label>
                        <Form.Control
                          as='select'
                          value={rating}
                          onChange={(e) => setRating(e.target.value)}
                        >
                          <option value=''>Select..</option>
                          <option value='1'>Poor</option>
                          <option value='2'>Fair</option>
                          <option value='3'>Good</option>
                          <option value='4'>Very Good</option>
                          <option value='5'>Excellent</option>
                        </Form.Control>
                      </Form.Group>
                      <Form.Group controlId='comment'>
                        <Form.Label>Comment</Form.Label>
                        <Form.Control
                          as='textarea'
                          row='3'
                          value={comment}
                          onChange={(e) => setComment(e.target.value)}
                        ></Form.Control>
                      </Form.Group>
                      <Button type='submit' variant='primary'>
                        Submit
                      </Button>
                    </Form>
                  ) : (
                    <Message>
                      Please <Link to='/login'>sign in</Link> to write a review
                    </Message>
                  )}
                </ListGroup.Item>
              </ListGroup>
            </Col>
          </Row>
        </>
      )}
    </>
  );
};

export default ProductScreen;
